import { type NextRequest, NextResponse } from "next/server"

// Mock items storage
const items: Map<number, any> = new Map([
  [
    1,
    {
      id: 1,
      user_id: 1,
      name: "Organic Apples",
      quantity: 5,
      quantity_unit: "pcs",
      category_id: 1,
      purchased: false,
      priority: "medium",
    },
  ],
  [
    2,
    {
      id: 2,
      user_id: 1,
      name: "Fresh Carrots",
      quantity: 1,
      quantity_unit: "kg",
      category_id: 1,
      purchased: false,
      priority: "high",
    },
  ],
  [
    3,
    {
      id: 3,
      user_id: 1,
      name: "Milk",
      quantity: 1,
      quantity_unit: "liter",
      category_id: 2,
      purchased: false,
      priority: "high",
    },
  ],
])

let nextId = 4

export async function GET(request: NextRequest) {
  try {
    const userItems = Array.from(items.values()).filter((item) => item.user_id === 1)
    return NextResponse.json(userItems)
  } catch (error) {
    return NextResponse.json({ error: "Failed to fetch items" }, { status: 500 })
  }
}

export async function POST(request: NextRequest) {
  try {
    const data = await request.json()

    const newItem = {
      id: nextId++,
      user_id: 1,
      ...data,
      purchased: false,
      created_at: new Date(),
    }

    items.set(newItem.id, newItem)

    return NextResponse.json(newItem)
  } catch (error) {
    return NextResponse.json({ error: "Failed to create item" }, { status: 500 })
  }
}
